import Checkbox from "@material-ui/core/Checkbox";
import FormControl from "@material-ui/core/FormControl";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormGroup from "@material-ui/core/FormGroup";
import FormHelperText from "@material-ui/core/FormHelperText";
import FormLabel from "@material-ui/core/FormLabel";
import { Theme } from "@material-ui/core/styles/createMuiTheme";
import createStyles from "@material-ui/core/styles/createStyles";
import withStyles, { WithStyles } from "@material-ui/core/styles/withStyles";
import TextField from "@material-ui/core/TextField";
import ArrowForwardIcon from "@material-ui/icons/ArrowForward";
import { FormikProps } from "formik";
import React from "react";
import StyledSelect from "../components/StyledSelect";
import SubmitButton from "../components/SubmitButton";
import { CALC_TYPES, DFT_OPTIONS } from "../constants/dftFormData";
import { IDftOptionsFormValues } from "../types/dft";

const styles = (theme: Theme) =>
  createStyles({
    formControl: {
      margin: theme.spacing.unit
    },
    textField: {
      margin: theme.spacing.unit
    }
  });

export interface IDftOptionsFormProps
  extends WithStyles<typeof styles>,
    FormikProps<IDftOptionsFormValues> {
  isEmailVerified: boolean;
  email: string;
  onSubmitOptions: any;
}
const DftOptionsForm: React.SFC<IDftOptionsFormProps> = ({
  classes,
  isEmailVerified,
  email,
  onSubmitOptions,
  ...props
}) => (
  <form onSubmit={props.handleSubmit}>
    <TextField
      className={classes.textField}
      error={!!props.errors.name}
      helperText={props.errors.name}
      fullWidth={true}
      label="Job Name"
      name="name"
      onChange={props.handleChange}
      required={true}
    />
    <StyledSelect
      errors={props.errors.dftOptions}
      isMulti={true}
      label="DFT Options"
      name="dftOptions"
      options={DFT_OPTIONS}
      onBlur={props.handleBlur}
      setFieldValue={props.setFieldValue}
    />
    <StyledSelect
      errors={props.errors.calcType}
      label="Calculation Type"
      name="calcType"
      options={CALC_TYPES}
      onBlur={props.handleBlur}
      setFieldValue={props.setFieldValue}
    />
    <FormControl component="fieldset" className={classes.formControl}>
      <FormLabel component="legend">Other Options</FormLabel>
      <FormGroup>
        <FormControlLabel
          control={
            <Checkbox
              checked={props.values.areFilesReused}
              onChange={props.handleChange}
              value="areFilesReused"
              name="areFilesReused"
            />
          }
          label="Reuse previously used files"
        />
        <FormControlLabel
          control={
            <Checkbox
              checked={props.values.isCube}
              onChange={props.handleChange}
              value="isCube"
              name="isCube"
            />
          }
          label="Output CUBE files (only for local)"
        />
        <FormControlLabel
          control={
            <Checkbox
              checked={props.values.isEmailRequested}
              onChange={props.handleChange}
              disabled={!isEmailVerified}
              value="isEmailRequested"
              name="isEmailRequested"
            />
          }
          label={email ? `Email ${email} when completed` : undefined}
        />
      </FormGroup>
      {!isEmailVerified && (
        <FormHelperText>
          Please verify {email},<br />
          then refresh the page.
        </FormHelperText>
      )}
    </FormControl>
    <SubmitButton
      error={Object.keys(props.errors).length !== 0}
      isSubmitting={props.isSubmitting}
      buttonText="Next"
      ButtonIcon={ArrowForwardIcon}
    />
  </form>
);
export default withStyles(styles)(DftOptionsForm);



